import React, { useEffect, useState } from 'react';
import { observer, useLocalStore } from 'mobx-react-lite';
import { Wrapper, Icon, FlexBox, Text, BorderBox, Button, Section, DropBox } from '../../../modules/globalStyle';
import { useStore } from '../../../store';
import { LpMining } from '../../../components/LpMining';
import { LpMiningHeader } from '../../../components/LpMiningHeader';
import { DepositViewForBSC } from '../../../components/DepositViewForBSC';
import { hooks } from '../../../lib/hooks';
import { generateNote, getCurrentCommitment } from '../../../lib/zSnarksWorker';
export const Deposit = observer((props) => {
  const { lang, god, dev, base } = useStore();

  return (
    <div style={{ display: props.isChosen? 'block' : 'none' }}>
    <Wrapper>
      <LpMiningHeader />
      {/* {god.AeolusV2 && <LpMining aeolus={god.AeolusV2} />} */}
      {/* <LpMining aeolus={god.Aeolus} /> */}
      {god.isPublicInfoLoaded && god.isPrivateInfoLoaded  && 
        <DepositViewForBSC
          chainID={god.currentChain.chainId}
          onConnect={() => { god.setShowConnecter(true) }}
          onApproveCYC={ async (token, value)=> {
            try {
              await god.approve({token, spender: god.currentChain.UniswapV2CycloneRouter.address, value})
            } catch (error) {
                throw new Error("Error while approving");
            }
          }}
          onDeposit={async () => {
            alert('start generating note...');
            const note = generateNote(god.currentChain.Coin.symbol, god.CurrentSet.id, god.currentChain.chainId);
            alert(note);
             god.currentNote = note;
            //  const res = await hooks.waitPendingPool({note: note});
            const res = true;
            //  alert(res);
            if(res) {
              try {
                await god.deposit({ commitment: getCurrentCommitment() });
              } catch (error) {
                throw new Error("Error while depositing");
              }
            }
            else
             throw new Error("Error while depositing");
          }}
          isAvaliable={true}
        />
      }
      {/* head */}
    </Wrapper>
    </div>
  );
});
